<template>
  <div
    :class="[
      'player',
      {
        pause: !pausetate,
      },
    ]"
  >
    <audio
      controls
      autoplay
      v-if="musicID"
      :src="`https://music.163.com/song/media/outer/url?id=${musicID.id}.mp3`"
      @timeupdate="timeupdate"
      ref="player"
      @play = "play"
      @toggleplay="toggleplay"
      @ended="ended"
    ></audio>
    <transition
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <div
        class="players"
        v-if="musicID"
        :style="{
          bottom: !$route.meta.hideNav ? '10vw' : '0vw',
        }"
      >
        <div><img :src="musicID.picUrl" /></div>
        <div @click="isFull = true">
          <p>{{ musicID.name }}</p>
          <p v-if="musicID">
            专辑:{{ musicID.song.album.name }} 歌手:<span
              v-for="art in musicID.song.artists"
              :key="art.id"
              >{{ art.name }}</span
            >
          </p>
        </div>
        <div
          class="playerfooter iconfont"
          v-bind:class="{ 'icon-ai05': pausetate, 'icon-bofang': !pausetate }"
          @click.stop="toggleplay"
        ></div>
      </div>
    </transition>
    <transition
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <full-page
        v-if="isFull"
        @close="isFull = false"
        :musicID="musicID"
        :pausetate="pausetate"
        :lyric="lyric"
        :currentTime="currentTime"
        @changetime="changeTime"
        @changePlayMusic="$emit('changePlayMusic', $event)"
        @toggleplay="toggleplay"
        :duration="duration"
        :Listplay="Listplay"
        :orders="orders"
        @randoms="randoms"
        ref="lyricpage"
        @remove="$emit('remove',$event)"
        @removeAll="$emit('removeAll',$event)"
      ></full-page>
      
    </transition>
  </div>
</template>
<script>
import FullPage from "@/components/FullPage";
export default {
  props: ["musicID", "Listplay"],
  data() {
    return {
      duration: 0,
      currentTime: 0,
      pausetate: true,
      isFull: false,
      lyric: null,
      orders: 1,
    };
  },
  components: {
    FullPage,
  },
  watch: {
    mvitem(a, b) {
      console.log(a, b);
      this.orders = a;
      if (a == true) {
        if (!this.pausetate) {
          this.$refs.player.pause();
        }
      } else if (a == false) {
        if (this.pausetate) {
          this.$refs.player.pause();
        }
      }
    },
    musicID(newMusic) {
      //侦听playMusic当前播放的音乐.获取歌词
      this.axios
        .get("/lyric", {
          params: {
            id: newMusic.id,
          },
        })
        .then((d) => {
          if (d.data && d.data.lrc && d.data.lrc.lyric) {
            this.lyric = d.data.lrc.lyric;
          }
        });
    },
  },
  methods: {
    ended() {
      if (this.orders == 3) {
        this.$refs.player.play();
      } else if (this.orders == 1) {
        this.$refs.lyricpage.prev();
      } else if (this.orders == 2) {
        let i = 0;
        this.Listplay.map((d, index) => {
          if (d.id == this.musicID.id) {
            //找到当前播放音乐下标
            i = index;
          }
        });
        if (this.orders == 2) {
          //随机播放时
          i = Math.floor(Math.random() * this.Listplay.length);
        }
        // console.log(this.Listplay);//上一曲 判断是不是第一曲
        let pmusic = this.Listplay[i];
        this.$emit("changePlayMusic", pmusic);
      }
    },
    randoms() {
      if (this.orders >= 3) {
        this.orders = 1;
      } else {
        this.orders++;
      }
    },
    timeupdate(event) {
      this.duration = event.target.duration;
      this.currentTime = event.target.currentTime;
    },
    changeTime(val) {
      let t = (val / 100) * this.duration; //得到播放时间
      this.$refs.player.currentTime = t; //改变播放时间
      console.log(val);
    },
    play(){
        this.pausetate = true
    },
    toggleplay() {
      // this.isActive = !this.isActive;
      // this.hasError = !this.hasError;
      // console.log(this.hasError);
      if (this.pausetate) {
        this.pausetate = false;
        console.log(this.pausetate)
        this.$refs.player.pause();
      } else {
        this.pausetate = true;
        console.log(this.pausetate)
        this.$refs.player.play();
      }
    },
  },
};
</script>
<style lang="less" scoped>
.player {
  position: absolute;
  top: 0;
  .players {
    position: fixed;
    width: 100%;
    height: 12vw;
    background: #f9f2f4;
    z-index: 999;
    display: flex;
    align-items: center;
    div {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      &:first-child {
        width: 10vw;
        img {
          width: 100%;
          border-radius: 50%;
          animation: round 3s 0s linear infinite;
        }
      }
      &:nth-child(2) {
        flex: 1;
        text-indent: 2vw;
        font-size: 3.4vw;
      }
    }
    .playerfooter {
      color: #7ce8f9;
    }
    .icon-ai05 {
      margin-left: 3vw;
      line-height: 100%;
      width: 10%;
      text-align: center;
      font-size: 35px;
    }
    .icon-bofang {
      margin-left: 3vw;
      line-height: 100%;
      width: 10%;
      text-align: center;
      font-size: 30px;
    }
  }
}
.pause {
  .players {
    div {
      &:first-child {
        width: 10vw;
        img {
          width: 100%;
          border-radius: 50%;
          animation-play-state: paused;
        }
      }
    }
  }
}
@keyframes round {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>