import Vue from 'vue'
import VueRouter from 'vue-router'
import Recommend from '@/views/Recommend.vue'

Vue.use(VueRouter)

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err)
}

const routes = [
  {
    path: '/',
    redirect:"/recommend"
  },
  {
    path: '/recommend/',
    name: 'Recommend',
    component: Recommend,
  },
  {
    path: '/hot/',
    name: 'Hot',
    props:true,
    component: ()=>import('@/views/Hot'),
  },
  {
    path: '/search',
    name: 'Search',
    meta:{
      hideNav:true
    },
    component: ()=>import('@/views/Search')
  },
  {
    path: '/mv',
    name: 'Mv',
    component: ()=>import('@/views/Mv')
  },
  {
    path: '/videoplay/:userId',
    name: 'VideoPlay',
    props:true,
    component: ()=>import('@/views/VideoPlay')
  },
  {
    path: '/dtlist',
    name: 'DtList',
    props:true,
    component: ()=>import('@/views/DtList')
  },
  {
    path: '/rankinglist',
    name: 'RankingList',
    component: ()=>import('@/views/RankingList')
  },
  {
    path: '/rankinglistson',
    name: 'RankingListson',
    props:true,
    component: ()=>import('@/views/RankingListson')
  },
  {
    path: '/listoflingers',
    name: 'ListofSingers',
    props:true,
    component: ()=>import('@/views/ListofSingers')
  },
  {
    path: '/mvranking',
    name: 'MvRanking',
    props:true,
    component: ()=>import('@/views/MvRanking')
  },
  {
    path: '/newsongs',
    name: 'NewSongs',
    component: ()=>import('@/views/NewSongs')
  },
  {
    path: '/lingerlists',
    name: 'LingerLists',
    component: ()=>import('@/views/LingerLists')
  },
]

const router = new VueRouter({
  routes
})

export default router
