<template>
  <div>
    <my-banner class="banners" :imgs="imgs" normalcolor="red" activecolor="blue"></my-banner>
  </div>
</template>
<script>
import MyBanner from "@/components/MyBanner"
export default {
  data() {
      return{
          imgs: [],
      }
  },
  components:{
      MyBanner
  },
  created() {
      this.$axios.get("/banner?type=2").then((data) => {
      this.imgs=data.data.banners.slice(0, 5);
    });
  },
};
</script>
<style lang="less" scoped>
.banners{
  width: 100%;
}
</style>