<template>
  <div>
    <div
      class="banner"
    >
      <transition-group
        v-bind:name="prefixClassName"
        class="containner"
        tag="div"
        @after-leave="afterLeave"
      >
        <div
          class="img"
          v-for="(im, index) in imgs"
          :key="index"
          v-show="showIndex == index"
        >
          <img :src="`${im.pic}`" />
        </div>
      </transition-group>
      <ol class="radius">
        <li
          v-for="(i, index) in imgs.length"
          @click="changeRadio(i)"
          :key="index"
          :class="{ active: i - 1 == showIndex }"
        ></li>
      </ol>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    imgs: {
      type: Array,
      default: function () {
        return [];
      },
    },
    normalcolor: {
      type: String,
      default: "yellow",
    },
    activecolor: {
      type: String,
      default: "purple",
    },
    
  },
  data() {
    return {
      showIndex: 0, //当前播放图片下标
      isEnd: true, //是否可以播放完成
      prefixClassName: "banner", // 过渡动画类名称前缀
    };
  },
  methods: {
    play() {
      setInterval(this.prev, 5000);
    },
    prev() {
      if (!this.isEnd) return;
      this.showIndex--;
      //this.prefixClassName="bottom";//right
      this.changeDirect(2);
      if (this.showIndex < 0) {
        //最前一张
        this.showIndex = this.imgs.length - 1;
      }
      this.isEnd = false; //改为false已经进去动画播放，不允许其他播放
    },
    next() {
      if (!this.isEnd) return;
      this.showIndex++;
      this.changeDirect(1);
      //this.prefixClassName="top"; //left

      if (this.showIndex >= this.imgs.length) {
        //最后一张
        this.showIndex = 0;
      }
      this.isEnd = false; //改为false已经进去动画播放，不允许其他播放
    },
    afterLeave() {
      this.isEnd = true;
    },
    changeRadio(i) {
      if (!this.isEnd) return;
      let index = i - 1; //2 第1张-> 第3张
      if (index > this.showIndex) {
        //this.prefixClassName="top" //left
        this.changeDirect(1);
      } else {
        // this.prefixClassName="bottom"; //right
        this.changeDirect(2);
      }
      this.showIndex = i - 1;
      this.isEnd = false; //改为false已经进去动画播放，不允许其他播放
    },
    changeDirect(di) {
      //如果di为1 left/top  di:2  right/bottom
      // if(this.direct=='cols'){
      //     this.prefixClassName='right'
      // }else{
      //     this.prefixClassName='bottom'
      // }
      if (this.direct == "cols") {
        this.prefixClassName = di == 1 ? "top" : "bottom";
      } else {
        this.prefixClassName = di == 1 ? "left" : "right";
      }
    },
  },
  //生命周期钩子
  mounted: function () {
    this.$nextTick(() => {
      this.play();
    });
  },
};
</script>
<style lang="less" scoped>
* {
  margin: 0px;
  padding: 0px;
}
ul,
ol {
  list-style: none;
}
img {
  display: block;
}
.banner {
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
  max-width: 750px;
  height: 35vw;
  position: relative;
}
.container {
  width: 100%;
  position: relative;
}
.img {
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
}
.img img {
  width: 100%;
}
.radius {
  display: block;
  position: absolute;
  z-index: 99;
  bottom: 2vw;
  left: 40vw;
  margin: 0px 10px auto;
}
.radius li {
  float: left;
  width: 2vw;
  margin-left: 1vw;
  background-color: antiquewhite;
  border-radius: 1vw;
  text-align: center;
  height: 2vw;
}
.radius li.active {
  background-color: blueviolet;
}
.bar {
  position: absolute;
  top: 50%;
  width: 40px;
  font-size: 30px;
  line-height: 40px;
  text-align: center;
  background-color: beige;
  border-radius: 50%;
  z-index: 99;
}
.btn {
  position: absolute;
  left: 320px;
  z-index: 999;
  padding: 5px;
  background-color: aqua;
  border: none;
  outline: none;
}
.prev {
  left: 10px;
}
.next {
  right: 10px;
}
/* 从左往右 */
.left-enter {
  transform: translateX(-100%);
}
.left-enter-to,
.left-leave {
  transform: translateX(0%);
}
.left-leave-to {
  transform: translateX(100%);
}
.left-enter-active,
.left-leave-active {
  transition: transform 2s linear;
}
/* 从右往左 */
.right-enter {
  transform: translateX(100%);
}
.right-enter-to,
.right-leave {
  transform: translateX(0%);
}
.right-leave-to {
  transform: translateX(-100%);
}
.right-enter-active,
.right-leave-active {
  transition: transform 2s linear;
}
/* 从上往下 */
.top-enter {
  transform: translateY(-100%);
}
.top-enter-to,
.top-leave {
  transform: translateY(0%);
}
.top-leave-to {
  transform: translateY(100%);
}
.top-enter-active,
.top-leave-active {
  transition: transform 2s linear;
}
/* 从下往上 */
.bottom-enter {
  transform: translateY(100%);
}
.bottom-enter-to,
.bottom-leave {
  transform: translateY(0%);
}
.bottom-leave-to {
  transform: translateY(-100%);
}
.bottom-enter-active,
.bottom-leave-active {
  transition: transform 2s linear;
}
</style>