<template>
  <div id="app">
    <nav-bar v-show="!$route.meta.hideNav"></nav-bar>
    <play
      :musicID="musicID"
      :Listplay="Listplay"
      @changePlayMusic="changePlayMusic"
      @remove="remove"
      @removeAll="removeAll"
    ></play>
    <transition
      enter-active-class="animate__animated animate__fadeInLeft"
      leave-active-class="animate__animated animate__fadeOutRight"
    >
      <keep-alive>
        <router-view @play="play" :musicID="musicID" />
      </keep-alive>
    </transition>
    <div class="padingBottom" v-if="musicID"></div>
  </div>
</template>
<script>
import NavBar from "@/components/NavBar";
import Play from "@/components/Play";
import defaultPic from "@/assets/music.png";
export default {
  data() {
    return {
      musicID: null,
      Listplay: [],
    };
  },
  components: {
    NavBar,
    Play,
  },
  methods: {
    changePlayMusic(event) {
      if (event) {
        this.musicID = event;
      }
    },
    play(event) {
      if (!event.picUrl && event.song && event.song.album) {
        event.picUrl = event.song.album.picUrl;
      }
      if (!event.picUrl) {
        event.picUrl = defaultPic;
      }
      //去重歌单
      if (Array.isArray(event)) {
        this.Listplay = this.Listplay.concat(event);
        let newArr = [];
        for (var i = 0; i < this.Listplay.length; i++) {
          if (newArr.indexOf(this.Listplay[i]) === -1) {
            newArr.push(this.Listplay[i]);
          }
        }
         this.Listplay = newArr;
         this.musicID = this.Listplay[0];
        return;
      }
      this.musicID = event;
      //console.log(this.musicID);
      let isPush = true;
      //加入歌单
      this.Listplay.map((d) => {
        if (d.id == event.id) {
          //歌曲已经存在
          isPush = false;
        }
      });
      if (isPush) this.Listplay.push(event);
    },
    remove(event) {
      if(this.musicID == this.Listplay[event-1]){
          this.musicID = this.Listplay[event]
          if(event == this.Listplay.length){
               this.musicID = this.Listplay[0]
          }
      }
      this.Listplay.splice(event - 1, 1);
    },
    removeAll(event){
      this.Listplay.splice(0, event.Listplays);
      this.Listplay.push(event.musicIDs)
    }
  },
};
</script>
<style>
#app {
}
.paddingTop {
  padding-top: 51px;
}
.padingBottom {
  background-color: #fff;
  height: 40px;
}
</style>
