<template>
  <!-- <ul
    class="navbar"
    :style="{
      position: pageYOffsets>42 ? 'fixed' :'absolute',
      top: pageYOffsets>42 ? '0px' : '42px',
    }"
  > -->
   <ul
    class="navbar"
    >
    <li><router-link to="/recommend"><span class="iconfont icon-shouye"></span></router-link></li>
    <li><router-link to="/hot"><span class="iconfont icon-regebang"></span></router-link></li>
    <li><router-link to="/rankinglist"><span class="iconfont icon-paihang"></span></router-link></li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      pageYOffsets: null,
    };
  },
  methods: {
    handleScroll() {
      this.pageYOffsets = window.pageYOffset;
    },
  },
  mounted() {
    // 滚动条的获取
    window.addEventListener("scroll", this.handleScroll, true);
  },
};
</script>
<style lang="less" scoped>
.navbar {
  display: flex;
  line-height: 10vw;
  width: 100%;
  background-color: white;
  z-index: 99;
  // position: absolute;
  // top: 51px;
  position: fixed;
  bottom: 0;
  li{
    flex: 1;
    text-align: center;
    a {
      width: 100%;
      display: inline-block;
      font-size: 4vw;
      color: #333;
      span{
        font-size: 6vw;
      }
      &.router-link-exact-active{
        border-bottom: 2px solid rgb(248, 106, 106);
        
        position: relative;
      }
    }
    &:nth-child(2){
      span{
        font-size: 5vw;
      }
    }
    &:last-child{
      span{
        font-size: 7vw;
      }
    }
  }
  border-bottom: 1px solid #ccc;
}
</style>