<template>
  <div
    :class="[
      'fullpage',
      {
        pause: !pausetate,
      },
    ]"
  >
    <div
      class="fullpagergba"
      :style="{
        background: `url(${musicID.picUrl}) no-repeat 0 0/100% 100%`,
      }"
    ></div>
    <div class="fullpagergbason">
      <div class="iconfonts">
        <span class="iconfont icon-xiala" @click="$emit('close')"></span>
      </div>
      <div class="picimg">
        <div class="m-song-turn">
          <img :src="musicID.picUrl" alt="" />
        </div>
      </div>
      <h2>{{ musicID.name }}</h2>
      <div class="lyric">
        <ul
          v-bind:style="{
            transform: `translateY(-${topyric - 100}px)`,
          }"
        >
          <li
            v-for="(item, index) in lyricArray"
            :key="index"
            :class="{ highlight: index + 1 == lyricIndex }"
            @click="$emit('changePlayMusic', song)"
            ref="lilyric"
          >
            {{ item.lyric }}
          </li>
        </ul>
      </div>

      <div class="control">
        <p class="playrange">
          <span>{{ currentTime | formatTime }}</span>
          <input
            type="range"
            max="100"
            :value="(currentTime / duration) * 100"
            @change="changeTime"
          />
          <span>{{ duration | formatTime }}</span>
        </p>
        <p class="setups">
          <span
            class="iconfont"
            @click="$emit('randoms')"
            :class="{
              'icon-hanhan-01-01': orders == 1,
              'icon-suijibofang': orders == 2,
              'icon-danquxunhuan': orders == 3,
            }"
          ></span>
          <span class="iconfont icon-shangyiqu" @click.stop="next"></span>
          <span
            class="iconfont"
            v-bind:class="{ 'icon-ai05': pausetate, 'icon-bofang': !pausetate }"
            @click="$emit('toggleplay')"
          ></span>
          <span class="iconfont icon-xiayiqu" @click.stop="prev"></span>
          <span
            class="iconfont icon-gedan"
            @click="showList = !showList"
          ></span>
        </p>
      </div>
      <transition
        enter-active-class="animate__animated animate__slideInUp"
        leave-active-class="animate__animated animate__slideOutRight"
      >
        <div class="ulpic" v-show="showList">
          <p>
            <span
              class="iconfont icon-xiala"
              @click="showList = !showList"
            ></span>
            <span
              class="iconfont icon-shanchu"
              @click="$emit('removeAll', {musicIDs:musicID,Listplays:Listplay.length })"
            ></span>
          </p>
          <ul class="Listplay" v-if="Listplay">
            <div>
              <li
                v-for="(songer, index) in Listplay"
                :key="index"
                :class="{ action: musicID.id == songer.id }"
              >
              <span @click="$emit('changePlayMusic', songer)">
                <span>{{ ++index }}</span>
                <span>{{ songer.name }}</span>
              </span>
                <span
                  @click="$emit('remove', index)"
                  class="iconfont icon-shanchu"
                ></span>
              </li>
            </div>
          </ul>
          <div class="notyet" v-if="Listplay.length == 0">暂无歌曲</div>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
export default {
  name: "FullPage",
  props: [
    "musicID",
    "pausetate",
    "lyric",
    "duration",
    "currentTime",
    "Listplay",
    "orders",
  ],
  data() {
    return {
      showList: false,
      lilyrics: [],
    };
  },
  methods: {
    //下一曲
    prev() {
      let i = 0;
      this.Listplay.map((d, index) => {
        if (d.id == this.musicID.id) {
          //找到当前播放音乐下标
          i = index;
        }
      });
      if (this.orders == 1) {
        i++;
        if (i >= this.Listplay.length) {
          i = 0;
        }
      } else if (this.orders == 2) {
        //随机播放时
        i = Math.floor(Math.random() * this.Listplay.length);
      } else if (this.orders == 3) {
        i++;
        if (i >= this.Listplay.length) {
          i = 0;
        }
      }
      // console.log(this.Listplay);//上一曲 判断是不是第一曲
      let pmusic = this.Listplay[i];
      this.$emit("changePlayMusic", pmusic);
      //$emit('',song)
    },
    // randoms() {
    //   let i = 0;
    //   this.orders++;
    //   this.Listplay.map((d, index) => {
    //     if (d.id == this.musicID.id) {
    //       //找到当前播放音乐下标
    //       i = index;
    //       // console.log(index);
    //     }
    //   });
    //   // console.log(this.Listplay);
    //   if (this.orders == 4) {
    //     this.orders = 1;
    //   }
    //   if (this.orders == 1) {
    //     //顺序
    //     i++;
    //     console.log(i);
    //   } else if (this.orders == 2) {
    //     //随机
    //     i = Math.floor(Math.random() * this.Listplay.length);
    //   } else if (this.orders == 3) {
    //     // 单曲
    //     i;
    //   }
    //   let pmusic = this.Listplay[i];
    //   this.$emit("changePlayMusic", pmusic);
    // },
    //上一曲
    next() {
      let i = 0;
      this.Listplay.map((d, index) => {
        if (d.id == this.musicID.id) {
          //找到当前播放音乐下标
          i = index;
        }
      });
      if (this.orders == 1) {
        i--;
        if (i < 0) {
          i = this.Listplay.length - 1;
          console.log(i);
        }
      } else if (this.orders == 2) {
        //随机播放时
        i = Math.floor(Math.random() * this.Listplay.length);
      } else if (this.orders == 3) {
        i--;
        if (i < 0) {
          i = this.Listplay.length - 1;
          console.log(i);
        }
      }
      // console.log(this.Listplay);
      let pmusic = this.Listplay[i];
      this.$emit("changePlayMusic", pmusic);
      //$emit('',song)
    },
    changeTime(event) {
      let val = event.target.value;
      this.$emit("changetime", val);
      // console.log(val);
    },
  },
  mounted() {
    //保证子组件渲染完成
    this.$nextTick(() => {
      this.lilyrics = this.$refs.lilyric;
    });
  },
  computed: {
    lyricArray() {
      //格局字符串歌词 转化为数组
      let lyric = this.lyric;
      if (!lyric) return []; //如果没有歌词返回空
      let arr = lyric.split("\n");
      arr.pop();
      let reg = /\[(\d+):(\d+\.\d+)\](.*)/;
      let lyricArr = arr.map((d) => {
        if (reg.test(d)) {
          let lyricObj = {
            time: parseInt(RegExp.$1) * 60 + parseFloat(RegExp.$2),
            lyric: RegExp.$3,
          };
          return lyricObj;
        }
      });
      return lyricArr;
    },
    lyricIndex() {
      //计算属性当前歌词下标
      let ctime = this.currentTime; //当前播放时间
      let lyric = this.lyricArray; //歌词计算属性数组
      let index = 0;
      for (let i = 0; i < lyric.length; i++) {
        if (i >= lyric.length - 1) {
          //最后一条歌词
          index = i;
          break;
        }
        if (lyric[i].time <= ctime && ctime < lyric[i + 1].time) {
          index = i;
          // if (this.lilyrics.length > 0) {
          //   //this.topyric = this.lilyrics[index].offsetTop
          // }
          break;
        }
      }
      return index;
    },
    topyric() {
      let i = this.lyricIndex;
      let t = 0;
      if (this.lilyrics.length > 0) {
        //this.topyric = this.lilyrics[index].offsetTop
        t = this.lilyrics[i].offsetTop;
      }
      return t;
    },
  },
  filters: {
    formatTime(val) {
      let minutes = parseInt(val / 60);
      minutes = minutes < 10 ? "0" + minutes : minutes;
      let sec = parseInt(val) - parseInt(minutes * 60);
      sec = sec < 10 ? "0" + sec : sec;
      return minutes + ":" + sec;
    },
  },
};
</script>
<style lang="less" scoped>
.fullpage {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: #fff;
  .fullpagergba {
    background-position: center;
    position: absolute;
    filter: blur(30px);
    top: 0px;
    left: 0px;
    height: 110%;
    width: 110%;
    margin: -5%;
    background-size: cover;
    z-index: -1;
  }
  .fullpagergbason {
    width: 100%;
    height: 100vh;
    position: relative;
    background-color: #6b7377dd;
    .iconfonts {
      display: flex;
      justify-content: center;
      .icon-xiala {
        color: #f7ecec;
        text-align: center;
        font-size: 30px;
      }
    }
    .picimg {
      img {
        display: block;
        width: 100px;
        position: absolute;
        left: 30px;
        right: 0;
        top: 30px;
        bottom: 0;
        z-index: 2;
        border-radius: 50%;
        animation: round 6s 0s linear infinite;
      }
      .m-song-turn {
        width: 160px;
        height: 160px;
        border-radius: 50%;
        box-shadow: 0 0 5px 5px;
        position: relative;
        margin: 0 auto;
        z-index: 2;
        background: url(../assets/12.png)
          no-repeat;
        background-size: contain;
      }
    }
    h2 {
      font-size: 5vw;
      margin: 3vh 0 3vh 0;
      text-align: center;
      color: #ddd;
      font-weight: 900;
    }
    .lyric {
      height: 36vh;
      overflow: hidden;
      color: #ffffff;
      ul {
        li {
          padding: 3px;
          font-size: 3.4vw;
          text-align: center;
          &.highlight {
            color: #0d9df0;
            font-weight: bold;
          }
        }
      }
    }
    .ulpic {
      position: fixed;
      bottom: 0;
      left: 0;
      background-color: #fff;
      color: #00c1fb;
      opacity: 0.6;
      width: 100%;
      z-index: 999;
      padding-bottom: 5vw;
      border-radius: 5vw 5vw 0 0;
      p {
        margin: 10px 0 10px 0;
        display: flex;
        justify-content: space-between;
        .icon-xiala {
          text-align: center;
        }
        span {
          margin-left: 3vw;
          margin-right: 5.6vw;
        }
      }
      .Listplay {
        // height: 70%;
        overflow: hidden;
        width: 100%;
        height: 55vw;
        div {
          height: 50vw;
          white-space: nowrap; /*文本不会换行，文本会在在同一行上继续*/
          overflow-x: auto;
          li {
            line-height: 8vw;
            padding: 0.2vw 0 0.2vw 2vw;
            display: flex;
            align-items: center;
            position: relative;
            color: #00c1fb;
            &.action {
              font-weight: bold;
              color: #128dde;
            }
            span {
              margin-right: 2vw;
              font-size: 4vw;
              &:first-child{
                width: 86.5vw;
              }
              // &:last-child {
              //   font-size: 4vw;
              //   position: absolute;
              //   right: 0;
              //   margin-left: 3vw;
              //   margin-right: 0px;
              //   line-height: 100%;
              //   width: 10%;
              // }
            }
          }
        }
      }
      .notyet {
        height: 6vw;
        text-align: center;
        font-size: 6vw;
        color: #128dde;
      }
    }
    .control {
      width: 100%;
      position: absolute;
      bottom: 0;
      .playrange {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        input {
          width: 70vw;
        }
        input[type="range"] {
          -webkit-appearance: none;
          height: 1vw;
          border-radius: 0.5vw;
          outline: none;
        }
        input[type="range"]::-webkit-slider-thumb {
          -webkit-appearance: none;
          position: relative;
          width: 10px;
          height: 10px;
          background: #128dde;
          border-radius: 50%;
          transition: 0.2s;
        }
        input[type="range"]::-webkit-slider-thumb:before,
        input[type="range"]::-webkit-slider-thumb:after {
          position: absolute;
          top: 3px;
          width: 2000px; /* 长度很长没关系，因为刚刚有用 overflow:hidden 了 */
          height: 4px;
          content: "";
          pointer-events: none; /* 让鼠标可以点击穿透伪元素，不然会点不到下面 */
          transition: 0.2s;
        }

        input[type="range"]::-webkit-slider-thumb:before {
          left: -1997px;
          background: #128dde;
        }
        input[type="range"]::-webkit-slider-thumb:after {
          left: 10px;
          background: #edc;
        }
        input[type="range"]:active::-webkit-slider-thumb:before,
        input[type="range"]:active::-webkit-slider-thumb:after {
          top: 6px;
        }

        input[type="range"]:active::-webkit-slider-thumb {
          width: 16px;
          height: 16px;
        }

        input[type="range"]:active::-webkit-slider-thumb:after {
          left: 16px;
        }
        span {
          color: #65e6fb;
          width: 10vw;
          font-size: 3.4vw;
        }
      }
    }
    .setups {
      color: #fff;
      height: 8vw;
      display: flex;
      justify-content: space-evenly;
      span {
        display: flex;
        align-items: center;
        line-height: 8vw;
        font-size: 6.5vw;
      }
      .icon-ai05 {
        font-size: 7.6vw;
      }
      .icon-bofang {
        margin: 0 2px 0 2px;
      }
    }
  }
}
.pause {
  .picimg {
    .m-song-turn{
      img {
        animation-play-state: paused;
      }
    }
  }
}
@keyframes round {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>