<template>
  <div class="recommed">
    <router-link tag="section" to="/search">
      <search-list :musicID="musicID"></search-list>
    </router-link>
    <banner></banner>
    <!-- <div class="paddingTop" v-if="!$route.meta.hideNav"></div> -->
    <div class="box1">
      <border-title>推荐歌单</border-title>
      <p @click="changeit">
        <span class="iconfont icon-huanyihuan"></span><span>换一换</span>
      </p>
    </div>
    <div class="recommned_songs">
      <picture-item
        v-for="item in recommned_songs"
        :key="item.id"
        :item="item"
        @picvideo="picvideo"
      ></picture-item>
      <loading v-if="recommned_songs.length <= 0"></loading>
      <!-- <loading v-if="!isLoading"></loading> -->
    </div>
      <border-title>MV排行</border-title>
    <div class="mv_sort">
      <picture-item
        v-for="mv in mv_sort"
        :key="mv.id"
        :item="mv"
        @picvideo="picvideo"
      ></picture-item>
      <loading v-if="mv_sort.length <= 0"></loading>
    </div>
    <border-title class="mvpaihang">最新音乐</border-title>
    <div class="newest">
      <newest-music
        v-for="newest in newest_music"
        :key="newest.id"
        :item="newest"
        @play="$emit('play', $event)"
        :musicID="musicID"
      ></newest-music>
      <loading v-if="newest_music.length <= 0"></loading>
    </div>
    <div class="musicbottom"></div>
  </div>
</template>

<script>
import BorderTitle from "@/components/BorderTitle";
import Banner from "@/components/Banner";
import PictureItem from "@/components/PictureItem";
import NewestMusic from "@/components/NewestMusic";
import Loading from "@/components/Loading";
import SearchList from "@/components/SearchList";
export default {
  name: "Recommed",
  props: ["musicID"],
  components: {
    BorderTitle,
    PictureItem,
    NewestMusic,
    Loading,
    SearchList,
    Banner,
  },
  data() {
    return {
      pageYOffsets: null,
      recommned_songs: [],
      mv_sort: [],
      newest_music: [],
      new_detail: [],
      newrecommned_songs: [],
      newindexall: 0,
      mv_indexall: [],
      mewalbum:[],
      // 定时器
      timer: null,
    };
  },
  created() {
    this.$axios.get("/personalized").then((data) => {
      this.newrecommned_songs = data.data.result;
      this.recommned_songs = data.data.result.slice(0, 6);
      console.log(this.recommned_songs);
    });
    this.$axios.get("/top/mv?limit=30").then((data) => {
      // this.mv_sort=data.data;
      console.log(data);
      this.mv_indexall = data.data.data;
      this.mv_sort = data.data.data.slice(0, 8).map((item) => {
        item.picUrl = item.cover;
        return item;
      });
    });
    this.$axios.get("/personalized/newsong").then((data) => {
      this.newest_music = data.data.result;
      console.log(this.newest_music);
    });
    this.$axios.get("/search?keywords=海阔天空&limit=24").then((data) => {
      this.new_detail = data.data.result;
    });
    // this.$axios.get("/album/newest").then((data) => {
    //   console.log(data);
    // });
    // this.$axios.get("album?id=127487697").then((data) => {
    //   console.log(data);
    // });
    ///personalized/newsong
  },
  methods: {
    picvideo(item) {
      console.log(item);
      if (String(item).length > 8) {
        this.$router.push({ name: "VideoPlay", params: { userId: item } });
      } else {
        this.$router.push({ name: "MvRanking", query: { mvId: item } });
        console.log(item);
      }
    },
    changeit() {
      // 延迟0.8秒执行加载数据
      this.recommned_songs = [];
      clearInterval(this.timer);
      this.timer = setTimeout(() => {
        this.newindexall = this.newindexall + 6;
        if (this.newindexall == 30) {
          this.newindexall = 0;
        }
        this.recommned_songs = this.newrecommned_songs.slice(
          this.newindexall,
          this.newindexall + 6
        );
      }, 800);
    },
  },
  //   beforeRouteEnter(to, from, next) {
  //     next((vm) => {
  //       vm.$axios.get("/personalized").then((data) => {
  //         console.log(data);
  //       });
  //     });
  //   },
};
</script>
<style lang="less" scoped>
// .paddingTop {
//   padding-top: 51px;
// }
.searchs {
  width: 90%;
  margin: 5px auto;
  background-color: #ebecec;
  border-radius: 15px;
  position: relative;
  input {
    line-height: 30px;
    font-size: 14px;
    background-color: transparent;
    border: none;
    width: 90%;
    margin: 0 auto;
    outline: transparent;
    &::-ms-input-placeholder {
      color: #c9c9c9;
    }
    &::-webkit-input-placeholder {
      color: #c9c9c9;
    }
  }
  .us {
    position: absolute;
    left: 0;
    top: 9px;
    margin: 0 8px;
    vertical-align: middle;
    width: 13px;
    height: 13px;
    background-image: url(data:image/svg+xml;base64,);
  }
}
.box1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  p {
    color: #17cdea;
    margin: 0 4vw 0 0;
    display: flex;
    align-items: center;
    span {
      &:nth-child(2) {
        font-size: 3.4vw;
      }
    }
  }
}
.recommned_songs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  div {
    width: 32%;
  }
  .loading {
    width: 100%;
  }
}
.mv_sort {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  div {
    width: 48%;
  }
  .loading {
    width: 100%;
  }
}
.musicbottom {
  width: 100%;
  height: 12vw;
}
.mvpaihang {
  margin-top: 0;
}
</style>